.todo-container {
    width: 30%;
    padding: 20px;
    text-align: center;
  }
  .gcheading{
    margin-top: -30px;
    height: 5%;
    font-size: 0.8rem;
  }
  .input-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  input {
    width: 70%;
    padding: 10px;
    border: 1px solid; 
    background-color: #3b2831;
    border-radius: 5px;
  }
  
  button {
    padding: 10px 20px;
    border: none;
    background-color: #28a745;
    color: white;
    cursor: pointer;
    border-radius: 5px;
  }
  
  button:hover {
    background-color: #218838;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
    cursor: pointer;
  }
  
  li.completed {
    text-decoration: line-through;
    color: gray;
  }
  